angular.module('flapperNews',
[
 'flapperNews.authentication',
 'flapperNews.slider',
 'flapperNews.posts',
 'flapperNews.home'
]);

angular
  .module('flapperNews.authentication', ['ui.router'])
  .config(config);

function config($stateProvider) {
  $stateProvider
    .state('login', {
      url: '/login',
      templateUrl: 'app/authentication/auth.login.html',
      controller: 'AuthCtrl',
      onEnter: ['$state', 'auth', goHome]
    })
    .state('register', {
      url: '/register',
      templateUrl: 'app/authentication/auth.register.html',
      controller: 'AuthCtrl',
      onEnter: ['$state', 'auth', goHome]
    });

  function goHome($state, auth) {
    if (auth.isLoggedIn()) {
      $state.go('home');
    }
  }
}

angular
.module('flapperNews.authentication')
.controller('AuthCtrl', ['$scope','$state','auth',AuthCtrl]);

function AuthCtrl($scope, $state, auth) {
  $scope.user = {};

  $scope.register = register;
  $scope.logIn = logIn;

  function register() {
    auth.register($scope.user).error(handleError).then(goHome);
  }

  function logIn() {
    auth.logIn($scope.user).error(handleError).then(goHome);
  }

  function handleError(error) {
    $scope.error = error;
  }

  function goHome() {
    $state.go('home');
  }
}

angular
  .module('flapperNews.authentication')
  .factory('auth', ['$http', '$window', factory]);

function factory($http, $window) {
  var auth = {};

  auth.saveToken = saveToken;
  auth.getToken = getToken;
  auth.isLoggedIn = isLoggedIn;
  auth.currentUser = currentUser;
  auth.register = register;
  auth.logIn = logIn;
  auth.logOut = logOut;

  function saveToken(token) {
    $window.localStorage['flapper-news-token'] = token;
  }

  function getToken() {
    return $window.localStorage['flapper-news-token'];
  }

  function isLoggedIn() {
    var token = auth.getToken();
    if (token) {
      var payload = JSON.parse($window.atob(token.split('.')[1]));
      return payload.exp > Date.now() / 1000;
    } else {
      return false;
    }
  }

  function currentUser() {
    if (auth.isLoggedIn()) {
      var token = auth.getToken();
      var payload = JSON.parse($window.atob(token.split('.')[1]));
      return payload.username;
    }
  }

  function register(user) {
    return $http.post('/register', user).success(function(data) {
      auth.saveToken(data.token);
    });
  }

  function logIn(user) {
    return $http.post('/login', user).success(function(data) {
      auth.saveToken(data.token);
    });
  }

  function logOut() {
    $window.localStorage.removeItem('flapper-news-token');
  }

  return auth;
}

angular
  .module('flapperNews.home', ['ui.router'])
  .config(['$stateProvider', '$urlRouterProvider', config]);

function config($stateProvider, $urlRouterProvider) {
  $stateProvider.state(
    'home', {
      url: '/home',
      templateUrl: 'app/home/home.template.html',
      controller: 'HomeCtrl',
      resolve: {
        postPromise: ['posts', function(postFactory) {
          return postFactory.getAll();
        }]
      }
    });
  $urlRouterProvider.otherwise('home');
}

angular
  .module('flapperNews.home')
  .controller('HomeCtrl', ['$scope', 'posts', 'auth', HomeCtrl]);

function HomeCtrl($scope, posts, auth) {
  $scope.isLoggedIn = auth.isLoggedIn;
  $scope.posts = posts.posts;
  $scope.addPost = addPost;
  $scope.incrementUpvotes = incrementUpvotes;
  $scope.decrementUpvotes = decrementUpvotes;

  function addPost() {
    if (!$scope.title || $scope.title === '') {
      return;
    }
    posts.create({
      title: $scope.title,
      link: $scope.link
    });
    $scope.title = '';
    $scope.link = '';
  }

  function incrementUpvotes(post) {
    posts.upvote(post);
  }

  function decrementUpvotes(post) {
    posts.downvote(post);
  }

}

angular
  .module('flapperNews')
  .controller('NavCtrl', ['$scope', 'auth', NavCtrl]);

function NavCtrl($scope, auth) {
  $scope.isLoggedIn = auth.isLoggedIn;
  $scope.currentUser = auth.currentUser;
  $scope.logOut = auth.logOut;
}

angular
  .module('flapperNews.posts', ['ui.router'])
  .config(['$stateProvider', config]);

function config($stateProvider) {
  $stateProvider.state(
    'posts', {
      url: '/posts/{id}',
      templateUrl: 'app/posts/posts.template.html',
      controller: 'PostsCtrl',
      resolve: {
        post: ['$stateParams', 'posts', function($stateParams, posts) {
          return posts.get($stateParams.id);
        }]
      }
    }
  );
}

angular
  .module('flapperNews.posts')
  .controller('PostsCtrl', ['$scope', 'posts', 'post', PostsCtrl]);

function PostsCtrl($scope, posts, post) {
  $scope.post = post;
  $scope.addComment = addComment;
  $scope.incrementCommentUpvotes = incrementCommentUpvotes;
  $scope.decrementCommentUpvotes = decrementCommentUpvotes;

  function addComment() {
    if ($scope.body === '') {
      return;
    }
    posts.addComment(post._id, {
      body: $scope.body,
      author: 'user',
    }).success(function(comment) {
      $scope.post.comments.push(comment);
    });
    $scope.body = '';
  }

  function incrementCommentUpvotes(comment) {
    posts.upvoteComment(post, comment);
  }

  function decrementCommentUpvotes(comment) {
    posts.downvoteComment(post, comment);
  }
}

angular
  .module('flapperNews.posts')
  .factory('posts', ['$http', 'auth', factory]);

function factory($http, auth) {
  var postFactory = {
    posts: []
  };

  postFactory.getAll = getAll;
  postFactory.create = create;
  postFactory.upvote = upvote;
  postFactory.downvote = downvote;
  postFactory.get = get;
  postFactory.addComment = addComment;
  postFactory.upvoteComment = upvoteComment;
  postFactory.downvoteComment = downvoteComment;

  function getAll() {
    return $http.get('/posts').success(function(data) {
      angular.copy(data, postFactory.posts);
    });
  }

  function create(post) {
    return $http.post('/posts', post, {
      headers: {
        Authorization: 'Bearer ' + auth.getToken()
      }
    }).success(function(data) {
      postFactory.posts.push(data);
    });
  }

  function upvote(post) {
    return $http.put('/posts/' + post._id + '/upvote', null, {
      headers: {
        Authorization: 'Bearer ' + auth.getToken()
      }
    }).success(function(data) {
      post.upvotes += 1;
    });
  }

  function downvote(post) {
    return $http.put('/posts/' + post._id + '/downvote', null, {
      headers: {
        Authorization: 'Bearer ' + auth.getToken()
      }
    }).success(function(data) {
      post.upvotes -= 1;
    });
  }

  function get(id) {
    return $http.get('/posts/' + id).then(function(res) {
      return res.data;
    });
  }

  function addComment(id, comment) {
    return $http.post('/posts/' + id + '/comments', comment, {
      headers: {
        Authorization: 'Bearer ' + auth.getToken()
      }
    });
  }

  function upvoteComment(post, comment) {
    return $http.put('/posts/' + post._id + '/comments/' + comment._id + '/upvote', null, {
      headers: {
        Authorization: 'Bearer ' + auth.getToken()
      }
    }).success(function(data) {
      comment.upvotes += 1;
    });
  }

  function downvoteComment(post, comment) {
    return $http.put('/posts/' + post._id + '/comments/' + comment._id + '/downvote', null, {
      headers: {
        Authorization: 'Bearer ' + auth.getToken()
      }
    }).success(function(data) {
      comment.upvotes -= 1;
    });
  }

  return postFactory;
}

angular.module('flapperNews.slider', ['ngAnimate']);

angular
  .module('flapperNews.slider')
  .controller('SliderController', SliderController);

function SliderController($scope) {
  $scope.images = [{
    src: 'img1.png',
    title: 'Pic 1'
  }, {
    src: 'img2.jpg',
    title: 'Pic 2'
  }, {
    src: 'img3.jpg',
    title: 'Pic 3'
  }, {
    src: 'img4.png',
    title: 'Pic 4'
  }, {
    src: 'img5.png',
    title: 'Pic 5'
  }];
}

angular
  .module('flapperNews.slider')
  .directive('slider', slider);

function slider() {
  return {
    restrict: 'AE',
    replace: true,
    scope: {
      images: '='
    },
    link: link,
    templateUrl: 'app/slider/slider.template.html'
  };

  function link(scope, elem, attrs) {
    scope.currentIndex = 0;
    scope.next = next;
    scope.prev = prev;
    scope.$watch('currentIndex', render);

    function next() {
      if (scope.currentIndex < scope.images.length - 1) {
        scope.currentIndex++;
      } else {
        scope.currentIndex = 0;
      }
    }

    function prev() {
      if (scope.currentIndex > 0) {
        scope.currentIndex--;
      } else {
        scope.currentIndex = scope.images.length - 1;
      }
    }

    function render() {
      scope.images.forEach(function(image) {
        image.visible = false;
      });
      scope.images[scope.currentIndex].visible = true;
    }
  }
}

